#index {
  min-height: 100vh;
}
.content {
  display: flex;
}
#nav {
  width: 200px;
  background: #222;
  padding-top: 20px;
  height: calc(100vh - 60px);
}
#nav > a {
  display: block;
  text-align: center;
  padding: 10px 0;
  font-size: 16px;
  color: rgb(192, 192, 192);
}
#nav > a.active {
  color: rgb(123, 233, 123);
}
.right-cont {
  width: calc(100vw - 200px);
  height: calc(100vh - 60px);
}

.right-cont .item {
  padding: 20px;
}

.avatar-uploader > .ant-upload {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.avatar-uploader img {
  width: 60px !important;
  height: 60px !important;
  border-radius: 50%;
}

.nickname {
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nickname .name {
  width: 50px;
}
.ant-modal-root {
  position: relative;
  z-index: 11111;
}
