*{margin: 0;padding: 0;}
ol,ul {list-style: none}
img {vertical-align: top;}
a {text-decoration: none;}
.fl { float: left; }
.fr { float: right; }
b,i { font-style: normal; }
.clearfix:after, .clearfix:before {
    content: '';
    display: table;
}
.clearfix:after {
    clear: both;
}
.container {min-height:100vh;}